.minoutre {
  .content {
    width: 695px;
    margin-top: 40px;
    height: 552px;
    position: relative;
    border: 1px dotted #d5d9d1;
    padding-right: 65px;
    padding-bottom: 30px;
    .border-border {
      width: 680px;
      height: 500px;
      border: 30px solid #fff;
      border-image: url('../../bundle/img/border_border.png') 30 30;
      margin: 10px;
      .item {
        margin-top: 50px;
        .ml60 {
          margin-left: 60px;
        }
        .mlT46 {
          margin: 30px 0 0 60px;
          display: inline-block;
          .input-wid95 {
            width: 95px;
            height: 25px;
          }
          .reverimg{
            position:absolute;
            width:12px;
            height:10px;
            right:1px;
            top:18px;
            background:url(../../bundle/img/icon.png) -495px -170px no-repeat;
            transform: rotate(180deg);
          }
        }
      }
      .handle {
        position: absolute;
        bottom: 80px;
        width: 100%;
      }
    }
    .triangle-bg {
      height: 54px;
      width: 54px;
      position: absolute;
      top: -5px;
      right: -1px;
      background: url(../../bundle/img/triangle.png) center center no-repeat;
    }

  }
}
